* {
  margin : 0;
  box-sizing: border-box;
  font-family: "Montessrat";
  scroll-behavior: smooth;
  
  --text: #040316;
--background: #fbfbfe;
--primary: #0d0b4c;
--secondary: #cba406;
--accent: #101828;

  
}
body {
  background-color: #f8f8ff;
}

