.container {
  width: 100%;
}
@media (min-width: 540px) {
  .container {
      max-width: 540px;
  }
}
@media (min-width: 720px) {
  .container {
      max-width: 720px;
  }
}
@media (min-width: 960px) {
  .container {
      max-width: 960px;
  }
}
@media (min-width: 1140px) {
  .container {
      max-width: 1140px;
  }
}
@media (min-width: 1320px) {
  .container {
      max-width: 1320px;
  }
}
@media (min-width: 1600px) {
  .container {
      max-width: 1600px;
  }
}
.mx-auto {
  margin-left: auto;
  margin-right: auto;
  padding: 0px 1rem;
}
.flex-container, .flex-st-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding:0.5rem 1rem;
  gap:0.5rem;
}
.flex-st-container {
 justify-content: flex-start;
}
a {
  text-decoration: none;
}